let that

class Tab {
  constructor(id) {
    that = this
    // Ëé∑ÂèñÂÖÉÁ¥†
    this.main = document.querySelector(id)
    this.add = this.main.querySelector(".tabAdd")
    this.ul = this.main.querySelector("header :first-child")
    this.tabcon = this.main.querySelector("main")
    console.log(this.tabcon)
    this.init()
  }

  // ÂàùÂßãÂåñ
  init() {
    this.updateNode()
    this.add.onclick = this.addTab
    for (let i = 0; i < this.lis.length; i++) {
      this.lis[i].index = i
      this.lis[i].onclick = this.toggleTab
    }
  }

  // Ëé∑ÂèñÊâÄÊúâÂÖÉÁ¥†
  updateNode() {
    this.lis = this.main.querySelectorAll("li")
    this.sections = this.main.querySelectorAll("section")
  }

  // ÂàáÊç¢
  toggleTab() {
    that.clearClass()
    this.className = "active"
    that.sections[this.index].className = "conactive"
  }

  // Ê∏ÖÈô§
  clearClass() {
    for (let i = 0; i < this.lis.length; i++) {
      this.lis[i].className = ""
      this.sections[i].className = ""
    }
  }

  // Ê∑ªÂä†
  addTab() {
    that.clearClass()
    let li = "<li class='active'><span>Êñ∞ÈÄâÈ°π</span><span>x</span></li>"
    let tab = "<section class='conactive'>ÊµãËØïn</section>"
    that.ul.insertAdjacentHTML("beforeend", li)
    that.tabcon.insertAdjacentHTML("beforeend", tab)
    that.init()
  }

  // Âà†Èô§
  removeTab() {
    console.log("remove")
  }

  // ‰øÆÊîπ
  editTab() {}
}

let Tab1 = new Tab("#tab")